import styled from 'styled-components';

const Cookies = () => {
    
    return ( 
        <ContenedorCookies>
            <div className="popup">
                {/* <img src="https://image.flaticon.com/icons/svg/2078/2078580.svg" alt=""> */}
                <h2>Acepta las politicas de privacidad para acceder al formulario de contacto</h2>
                <p>Para cumplir la normativa europea de privacidad bla bla bla..... si quieres mÃ¡s informacion entra en ver politicas.</p>
                <p>Tranquilo que yo no usarÃ© tus datos ni los compartire con terceros.</p>
                <div className="btn-container">
                    <a className="btn" href="/contacto">Aceptar</a>
                    <a className="btn ghost" href="/politicas-de-privacidad">Ver las politicas de privacidad</a>
                </div>
            </div>
            </ContenedorCookies>
        
        
     );
}
 

const ContenedorCookies=styled.div`
    background-color: #F0A500;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 91.5vh;
	margin: 0;

    .popup{
    background-color: #fff;
	box-shadow: -4px 4px 5px rgba(0, 0, 0, 0.3);
	border-radius: 5px;
	padding: 20px 30px;
	text-align: center;
	max-width: 500px;
    }
    p {
	color: #555;
    }
    .btn-container {
	display: flex;
	align-items: center;
	justify-content: center;
    }
    .btn {
	background-color: #F0A500;
	border: 0;
	border-radius: 5px;
	box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
	color: #fff;
	cursor: pointer;
	font-size: 18px;
	padding: 15px 30px;
	position: relative;
	margin: 10px;
    }

    .btn:active {
	box-shadow: none;
	transform: scale(0.98);
    }

    .btn:focus {
        outline: none;
    }

    .btn.ghost {
	background-color: #fff;
	color: #000;
	box-shadow: none;
    }

    .btn .ghost{
	background-color: #3498db;
	border: 0;
	border-radius: 5px;
	color: #fff;
	font-size: 10px;
	padding: 3px 7px;
	position: absolute;
	top: -10px;
	right: -10px;
	transform: rotate(10deg);
    }
    .popup img {
	width: 70px;
    }
    
`;

export default Cookies;