import styled from 'styled-components';
import Olas from '../animaciones/Olas';
import OlasTop from '../animaciones/OlasTop';

const Portada = () => {
    return (         
              <ContenedorPortada> 
                  <OlasTop />
                  <CajaPortada>
                  <ContenedorTextosPortada>
                    <h1>Jairo GarcÃ­a GonzÃ¡lez</h1>
                    <h2>Front End Developer</h2>
                   </ContenedorTextosPortada>                           
                    <ImagenPortada src="/desing/portada/brodes.png" alt="diseÃ±o portada JairoGarGon"></ImagenPortada>
                        <Eslogan>DiseÃ±ando soluciones - Solucionando diseÃ±os</Eslogan>
                        <LlamadaAccion href="/conoceme">ConozcÃ¡monos</LlamadaAccion>
                  </CajaPortada>
                        <Olas />
              </ContenedorPortada>
     );
}
const ContenedorPortada= styled.div`
    margin:0;
    padding:0;
    width:360px; 
    align-content:center;
    text-align:center;
    height:600px;
  @media (min-width:768px ) {
        width:auto;
        height:auto;
}
`;

const CajaPortada=styled.div`
        @media (min-width:768px ) {
           display:grid;
            grid-template-columns:repeat(2,1fr)     
          }    
`;
const ContenedorTextosPortada= styled.div` 
    h1{
        color:#1A1C20;
        font-size:2em;
        margin:0px;
    } 
    h2{
        color:#CF7500;
        font-size:1.7em;
        margin: 0px;
        text-shadow: 0 3px 3px black;
    }
    @media (min-width:768px ) {
      margin:0 70px;
      position:relative;
      left:30px;
        h1{
          font-size:5em;
          margin-top:100px;
        }
        h2{
          font-size:3.5em;
          margin-top:50px;
        }
}
`;
const Eslogan=styled.p`
              font-size:14px;
              color:#7F7F7F;
              font-weight:700;
      @media(min-width:768px){
          font-size:30px;
          position:relative;
          top:-220px;
          left:30px;
      }
`;
const LlamadaAccion= styled.a`
        padding:7px 50px 4px;
        color: #1A1C20;
        font-size:26px;
        font-weight:700;
        text-decoration:none;
        position: relative;
        overflow: hidden;
        z-index: 1;
        transition: color 150ms ease-in-out;
        border:2px solid #CF7500;
        border-radius:10px;
        cursor: pointer;
      @media(min-width:768px){
        font-size:38px;
        width:300px;
        position:relative;
        left:-650px;
        top:-120px;
        margin-top:30px;
        :hover {
          background-color:#CF7500;
          color: #fafafa;
          border-radius:10px;
          border:none;
        }

      }
`;
const ImagenPortada= styled.img`
        height:330px;
        width:330px;
    @media(min-width:768px){
        width:600px;
        height:600px;
        margin:0 150px;
    }
`;

export default Portada;