import styled from 'styled-components';
import Formulario from './Formulario';

const Contacto = () => {
    return(
     
       <ContenedorContacto>
          <Formulario /> 
           <img src="/desing/contacto/bro.png" alt="vector contacto"></img>
       </ContenedorContacto>
    );
}

const ContenedorContacto=styled.div`
    display:grid;
    padding:60px 0 0 0;
    img{
        position: relative;
        display:flex;
        left:40px;
        height:260px;
    }
    @media(min-width:768px){
        padding:230px 250px;
        img{
            height:auto;
            left:1100px;
            position:fixed;
            margin-top:125px;
        }
    }
 `;
 export default Contacto;