import {NavLink} from 'react-router-dom';
import styled from 'styled-components';

const Menu = () => {
    return ( 
          <ContenedorNav>
            <NavLink to="/" exact={true}></NavLink>
            <NavLink to="/conoceme">Conoceme</NavLink>
            <NavLink to="/formacion">Formacion</NavLink>
            <NavLink to="/proyectos">Proyectos</NavLink>
            <NavLink to="/cookies">Contacto</NavLink>
          </ContenedorNav>
     );
}

const ContenedorNav =styled.nav`
    padding:15px 0 3px;
    background-color:#F0A500;
    text-align:center;
    margin:0;
    width:100%;
    a{
        text-decoration:none;
        font-weight: 700;
        color:#1A1C20;
        margin:10px 20px 0 20px;
        font-size:0px;
    }  
    a:nth-child(1){
        display:none;
    }
    a:nth-child(2)::after{
        content:url("/desing/conoceme/conoceme-ico2.png");
    }
    a:nth-child(3)::after{
        content:url("/desing/formacion/educacion.png");
    }
    a:nth-child(4)::after{
        content:url("/desing/proyectos/portfolio.png");
    }
    a:nth-child(5)::after{
        content:url("/desing/contacto/formulario1.png");
    }
    a.active{
        border-bottom: 2px solid #CF7500;
        padding-bottom:3px;
        position:relative;
        color:#1A1C20;
        font-size:14px;
        color:#fafafa;
    }
    @media (min-width:768px ) {
        padding:17px 0 20px;
        a{  
            font-size:30px;
            margin:0 100px;
            cursor: pointer;
        }
        a:hover{
            color:#fafafa;
        }
        a.active{
            font-size:40px;
        }
        a:nth-child(2)::after{
        content:none;
        }
        a:nth-child(3)::after{
            content:none;
        }
        a:nth-child(4)::after{
            content:none;
        }
        a:nth-child(5)::after{
            content:none;
        }
}
`;

export default Menu;