const Proyectos=styled.div`
.wrapper{
    width: 80%;
    margin: 0 auto;
    max-width: 70rem;
  }
  @media(min-width:768px){
      width:80%;
      margin:auto;
  }
  .cols{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .col{
    width: calc(25% - 6rem);
    margin: 1rem;
    cursor: pointer;
  }
  .container{
    transform-style: preserve-3d;
    perspective: 1000px;
  }
  .front,
  .back{
    background-size: cover;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.25);
    border-radius: 10px;
    background-position: center;
    transition: transform .7s cubic-bezier(0.4, 0.2, 0.2, 1);
    backface-visibility: hidden;
    text-align: center;
    min-height: 220px;
    width:280px;
    margin:10px auto 0;
    height: auto;
    border-radius: 10px;
    color: #fff;
    font-size: 1.5rem;
  }
  @media(min-width:768px){
    .front,
    .back{
      min-height: 250px;
      width:300px;
  }
  }
  .front{
    background: linear-gradient(45deg, #F0A500 0%,#CF7500 100%);
  }
  .front:after{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      content: '';
      display: block;
      opacity: .6;
      background-color: #CF7500;
      backface-visibility: hidden;
      border-radius: 10px;
  }
  .container:hover .front,
  .container:hover .back{
      transition: transform .7s cubic-bezier(0.4, 0.2, 0.2, 1);
  }
  .back{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: linear-gradient(90deg, #CF7500 0%,#F0A500 100%);
       a{
          text-decoration:none;
          color:#1A1C20;
          padding:5px 15px;
          border-radius:7px;
          border:2px solid #CF7500;
          background-color:#fafafa; 
      }
      a:hover{
        color:#fafafa;
        background: linear-gradient(90deg, #F0A500 0%, #CF7500 100%);
      }
  }
  .inner{
      transform: translateY(-50%) translateZ(60px) scale(0.94);
      top: 50%;
      position: absolute;
      left: 0;
      width: 100%;
      padding: 2rem;
      box-sizing: border-box;
      outline: 1px solid transparent;
      perspective: inherit;
      z-index: 2;
  }
  .container .back{
      transform: rotateY(180deg);
      transform-style: preserve-3d;
  }
  .container .front{
      transform: rotateY(0deg);
      transform-style: preserve-3d;
  }
  .container:hover .back{
      transform: rotateY(0deg);
      transform-style: preserve-3d;
  }
  .container:hover .front{
      transform: rotateY(-180deg);
      transform-style: preserve-3d;
  }
  .front .inner p{
    font-size: 2rem;
    margin-bottom: 2rem;
    position: relative;
  }
  .front .inner p:after{
    content: '';
    width: 4rem;
    height: 2px;
    position: absolute;
    background: #1A1C20;
    display: block;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: -.75rem;
  }
  .front .inner span{
    font-weight:700;
    color: #1A1C20;
  }
  
  @media screen and (max-width: 64rem){
    .col{
      width: calc(33.333333% - 2rem);
    }
  }
  @media screen and (max-width: 48rem){
    .col{
      width: calc(50% - 2rem);
    }
  }
  @media screen and (max-width: 32rem){
    .col{
      width: 80%;
      margin: 0 0 2rem 0;
    }
  }
  
`; 
const Logo=styled.img`
width:50px;
  `;
export default CajaProyectos;