import React,{useState} from 'react';
import styled from 'styled-components';
import db from './../firebase/firebaseConfig';

const Formulario = () => {
    const [nombre,cambiarNombre]=useState('');
    const [correo,cambiarCorreo]=useState('');
    const [mensaje,cambiarMensaje]=useState('');

    const onSubmit=(e)=>{
        e.preventDefault();
        db.collection('usuarios').add({
            nombre:nombre,
            correo:correo,
            mensaje:mensaje
        }).then(()=>{
            alert('Se envio correctamente')
        }).catch(()=>{
            alert('Hubo algÃºn error')
        })
        cambiarNombre('');
        cambiarCorreo('');
        cambiarMensaje('');
    }
    return ( 

        <CajaFormulario action="" onSubmit={onSubmit}>
            <Input
                type="text"
                name="nombre"
                value={nombre}
                onChange={(e)=>cambiarNombre(e.target.value)}
                placeholder="Nombre"
                required
            />
            <Input
                type="email"
                name="correo"
                value={correo}
                onChange={(e)=>cambiarCorreo(e.target.value)}
                placeholder="Correo"
                required
            />
            <Input
                type="textarea"
                name="mensaje"
                value={mensaje}
                onChange={(e)=>cambiarMensaje(e.target.value)}
                placeholder="Mensaje"
                required
            />
            <Boton type="submit">Enviar</Boton>
        </CajaFormulario>
     );
}
const CajaFormulario=styled.form`
    margin:0 40px;
`;
const Input = styled.input`
    padding: 10px;
    border: 2px solid rgba(0,0,0,.2);
    border-radius: 3px;
    width:250px;
    margin:5px auto 10px;
    transition: .2s ease all;
    outline: none;
    text-align: center;
    &:focus {
        border: 2px solid #F0A500;
    }
    @media(min-width:768px){
        padding:25px;
        width:500px;
        font-size:22px;
        margin-left:150px;
    }
`;
const Boton = styled.button`
    padding: 10px 50px;
    border: none;
    cursor: pointer;
    border-radius: 3px;
    transition: .3s ease all;
    outline: none;
    background: #F0A500;
    color: #fff;
    font-size: 12px;
    margin:30px 70px 50px;
    &:hover {
        background: #CF7500;
    }
    @media(min-width:768px){
        font-size:22px;
        padding:20px 160px;
        margin:40px 230px;
    }
`;

export default Formulario;