import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import styled from 'styled-components';
import Menu from './componentes/Menu';
import Portada from './componentes/Portada';
import Conoceme from './componentes/Conoceme';
import Formacion from './componentes/Formacion';
import Proyectos from './componentes/Proyectos';
import Contacto from './componentes/Contacto';
import Lena from './componentes/Lena';
import Cookies from './componentes/Cookies';
import Politicas from './componentes/Politicas';

function App() {
  return (
      <BrowserRouter>
        <Aplicacion>     
          <main>
          <Menu /> 
            <Route path="/" exact={true} component={Portada} />
            <Route path="/cookies" component={Cookies} />
            <Route path="/conoceme" component={Conoceme} />
            <Route path="/formacion" component={Formacion} />
            <Route path="/proyectos" component={Proyectos} />  
            <Route path="/contacto" component={Contacto} />
            <Route path="/lena" component={Lena} />
            <Route path="/politicas-de-privacidad" component={Politicas} />
          </main>
        </Aplicacion>    
      </BrowserRouter>
  );
}

const Aplicacion = styled.div`
    font-family: "Roboto", sans-serif;
    background-color:#fafafa;
    width:360px;
    height:640px;
    @media(min-width:768px){
      width:auto;
      height:auto;
    }
`;

export default App;