import styled from 'styled-components';

const Skills = () => {
    return ( 
        <CajaSkills>
          <p>Habilidades</p>
        <div className="SkillBox">
		  <div className="SkillBar">
			<div className="Skill-HTML"> 
			  <span className="Skill-Area ">HTML</span>
			  <span className="PercentText ">80%</span>
			</div>
		  </div>	  
		  <div className="SkillBar">
			<div className="Skill-CSS">
			  <span className="Skill-Area ">CSS/CSS3</span>
			  <span className="PercentText ">70%</span>
			</div>
		  </div>		 
		  <div className="SkillBar">
			<div className="Skill-JS">
			  <span className="Skill-Area ">Javascript</span>
			  <span className="PercentText ">65%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-React">
			  <span className="Skill-Area ">React</span>
			  <span className="PercentText ">50%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-JAVA">
			  <span className="Skill-Area ">JAVA </span>
			  <span className="PercentText ">25%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-PHP">
			  <span className="Skill-Area ">PHP </span>
			  <span className="PercentText ">35%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-SQL">
			  <span className="Skill-Area ">MySQL </span>
			  <span className="PercentText ">40%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-FireBase">
			  <span className="Skill-Area ">FireBase</span>
			  <span className="PercentText ">35%</span>
			</div>
		  </div>
		  <div className="SkillBar">
			<div className="Skill-WordPress">
			  <span className="Skill-Area ">WordPress</span>
			  <span className="PercentText ">70%</span>
			</div>
		  </div>	
		</div>
        </CajaSkills>
     );
}
const CajaSkills=styled.div`
p{
  position:absolute;
  border: 2px solid #CF7500;
  background-color:#fafafa;
  z-index:5;
  font-size:14px;
  font-weight:600;
  top:400px;
  padding:3px 5px;
  border-radius:7px;
  left:180px;
}
@media(min-width:768px){
  p{
    font-size:26px;
    top:16vh;
    left:70vw;
  }  
}
.SkillBox {
    font-size: 10px;
    width: 70%;
    height: auto;
    border-radius:20px;
    position:absolute;
    z-index:3;
    left:100px;
    top:440px;
}
@media(min-width:768px){
  .SkillBox{
    font-size: 22px;
    width: 35%;
    margin: 15px auto 0;
    background-color: #fafafa;
    border: 1px solid #CF7500;
    padding: 2px; 
    left:1100px;
    top:210px;
  }
    
}
.SkillBar {
    width: 95%;
    height: 17px;
    position: relative;
    background: #f0a400b7;
    margin: 3px auto;
    border-radius:10px;
}
@media(min-width:768px){
    .SkillBar{
      height: 30px;
      margin:15px;
      border:1px solid #F0A500;
    }
}
.Skill-HTML {
    width: 80%;
    animation: Animate-HTML 4s;
    height: 17px;
    position: absolute;
    background-color: #F16529;
    border-radius:10px;
}
@keyframes Animate-HTML {
    from {
    width: 10px;
}
to {
    width: 80%}
}
.Skill-CSS {
    animation: Animate-CSS 5s;
    width: 70%;
    height: 17px;
    position: absolute;
    background-color: #2965f1;
    border-radius:10px;
}
@keyframes Animate-CSS {
    from {
    width: 10px;
}
to {
    width: 70%}
}
.Skill-JS {
    animation: Animate-JS 4s;
    width: 65%;
    height: 17px;
    position: absolute;
    background-color: #f0db4f;
    border-radius:10px;
}
@keyframes Animate-JS {
    from {
    width: 10px;
}
to {
    width: 65%}
}
.Skill-React {
    animation: Animate-React 5s;
    width: 50%;
    height: 17px;
    position: absolute;
    background-color: #61DBFB;
    border-radius:10px;
}
@keyframes Animate-React {
    from {
    width: 10px;
}
to {
    width: 50%}
}
.Skill-JAVA {
    animation: Animate-JAVA 4s;
    width: 25%;
    height: 17px;
    position: absolute;
    background-color: #ED1D25;
    border-radius:10px;
}
@keyframes Animate-JAVA {
    from {
    width: 10px;
}
to {
    width: 25%}
}
.Skill-PHP {
    animation: Animate-PHP 4s;
    width: 35%;
    height: 17px;
    position: absolute;
    background-color: #787CB5;
    border-radius:10px;
}
@keyframes Animate-PHP {
    from {
    width: 10px;
}
to {
    width: 35%}
}
.Skill-SQL {
    animation: Animate-SQL 4s;
    width: 40%;
    height: 17px;
    position: absolute;
    background-color: #F29111;
    border-radius:10px;
}
@keyframes Animate-SQL {
    from {
    width: 10px;
}
to {
    width: 40%}
}
.Skill-FireBase {
    animation: Animate-FireBase 4s;
    width: 35%;
    height: 17px;
    position: absolute;
    background-color: #FFCB2B;
    border-radius:10px;
}
@keyframes Animate-FireBase {
    from {
    width: 10px;
}
to {
    width: 35%}
}
.Skill-WordPress {
    animation: Animate-WordPress 4s;
    width: 70%;
    height: 17px;
    position: absolute;
    background-color: #00749C;
    border-radius:10px;
}
@keyframes Animate-WordPress {
    from {
    width: 10px;
}
to {
    width: 70%}
}
@media (min-width: 768px) {
  .Skill-HTML, .Skill-CSS, .Skill-JS, .Skill-React, .Skill-JAVA, .Skill-PHP, .Skill-SQL, .Skill-FireBase, .Skill-WordPress{
    height: 30px;
  }
}
.Skill-Area {
    z-index: 1;
    float: left;
    margin-top: 3px;
    margin-left: 5px;
    color:#1A1C20;
    font-size: 10px;
    font-weight:600;
}
.PercentText {
    z-index: 3;
    position: relative;
    padding-right: 5px;
    margin-top: 3px;
    float: right;
    color:#1A1C20;
    font-size: 9px;
    font-weight:600;
}
@media (min-width: 768px) {
  .Skill-Area, .PercentText{
    font-size: 20px;
  }
}
`; 

export default Skills;