import styled from 'styled-components';
import CajaProyectos from './CajaProyectos';

const Proyectos = () => {
    return (                
               <div>
                   <Monigote className="monigote" src="/desing/proyectos/Component.png" alt="Vector monigote JairoGarGon"></Monigote> 
                    <CajaProyectos />
                    
                  </div>
     );
} 

const Monigote=styled.img`
         height:260px;
         margin: 0px 50px;
         position:relative;
         z-index:0;

     @media(min-width:768px){
         left:730px;
         height:auto;
         position:absolute;
         top:620px;
         
     }
`;

export default Proyectos;